@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap");
html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

* {
  margin: 0;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
  color: hsl(234, 29%, 20%);
  background-color: hsl(235, 18%, 26%);
}

@media (min-width: 480px) {
  .container-center {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
  }
}
@media (min-width: 768px) {
  .container-center {
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

img {
  width: 100%;
}

.hidden {
  display: none;
}

.error {
  color: rgb(255, 98, 87);
}

.invalido:focus {
  border-color: rgb(255, 98, 87);
  color: rgb(255, 98, 87);
  background: rgba(255, 98, 87, 0.38);
}

.valido:focus {
  border-color: 2px solid #789b78;
  background-color: rgba(78, 227, 48, 0.3294117647);
  color: black;
}

