.container {
  background-color: hsl(0, 0%, 100%);
}
@media (min-width: 480px) {
  .container {
    max-width: 70rem;
    display: flex;
    align-items: center;
    border-radius: 2rem;
    padding: 2rem;
    gap: 2rem;
  }
}
@media (min-width: 768px) {
  .container {
    max-width: 90rem;
    display: flex;
    align-items: center;
    padding: 2rem;
    border-radius: 2rem;
  }
}

@media (min-width: 480px) {
  .container-img {
    order: 2;
  }
}
@media (min-width: 768px) {
  .container-img {
    order: 2;
  }
}
.container-img .img-desktop {
  display: none;
}
@media (min-width: 480px) {
  .container-img .img-desktop {
    display: block;
  }
}
@media (min-width: 768px) {
  .container-img .img-desktop {
    display: block;
  }
}
@media (min-width: 480px) {
  .container-img .img-mobile {
    display: none;
  }
}
@media (min-width: 768px) {
  .container-img .img-mobile {
    display: none;
  }
}

.container-info {
  width: 85%;
  max-width: 45rem;
  margin: 0 auto;
}
@media (min-width: 480px) {
  .container-info {
    order: 1;
    padding: 0 2rem;
  }
}
@media (min-width: 768px) {
  .container-info {
    order: 1;
    padding: 0 4rem;
  }
}
.container-info h1 {
  font-size: 3.8rem;
  font-weight: 700;
  color: hsl(234, 29%, 20%);
  margin: 2rem 0;
}
@media (min-width: 768px) {
  .container-info h1 {
    font-size: 5rem;
  }
}
.container-info p {
  color: hsl(234, 29%, 20%);
  font-size: 1.5rem;
}
@media (min-width: 768px) {
  .container-info p {
    font-size: 1.6rem;
  }
}
.container-info .description-list {
  padding: 0;
  list-style: none;
  margin: 2rem 0;
  font-size: 1.5rem;
}
@media (min-width: 768px) {
  .container-info .description-list {
    margin: 3rem 0;
    font-size: 1.6rem;
  }
}
.container-info .description-list li {
  padding-left: 3rem;
  background-image: url(/build/img/icon-list.svg);
  background-position: 0 0;
  background-size: 1.7rem 1.7rem;
  background-repeat: no-repeat;
  margin-bottom: 1.5rem;
  font-size: 1.5rem;
}
@media (min-width: 768px) {
  .container-info .description-list li {
    font-size: 1.6rem;
    background-size: 1.8rem 1.8rem;
  }
}
.container-info .description-list li:last-of-type {
  margin-bottom: 0;
}

.container-form .label-field {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  font-size: 1.2rem;
  font-weight: 700;
  margin-bottom: 1.2rem;
}
.container-form #email {
  width: 100%;
  padding: 1.5rem;
  border-radius: 1rem;
  border: 1px solid hsl(231, 7%, 60%);
  outline: none;
}
@media (min-width: 480px) {
  .container-form #email {
    padding: 1.3rem;
  }
}
@media (min-width: 768px) {
  .container-form #email {
    padding: 2rem;
  }
}
.container-form #email:focus {
  border: 2px solid hsl(234, 29%, 20%);
}

.container-modal {
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  padding: 7rem 0 2rem;
  background-color: hsl(0, 0%, 100%);
}
@media (min-width: 480px) {
  .container-modal {
    max-height: 50rem;
  }
}
@media (min-width: 768px) {
  .container-modal {
    max-height: 50rem;
    justify-content: center;
    padding: 0;
  }
}
.container-modal .content-modal {
  width: 85%;
  max-width: 50rem;
  margin: 0 auto;
  z-index: 3;
}
@media (min-width: 768px) {
  .container-modal .content-modal {
    width: 80%;
  }
}
.container-modal .content-modal h1 {
  font-size: 4rem;
  margin: 2rem 0;
  font-weight: 700;
  line-height: 1;
}
@media (min-width: 480px) {
  .container-modal .content-modal h1 {
    font-size: 5rem;
  }
}
@media (min-width: 768px) {
  .container-modal .content-modal h1 {
    font-size: 5rem;
  }
}
.container-modal .content-modal #email-registrado {
  font-weight: 700;
}
.container-modal .content-modal .btn-modal {
  margin: 2rem 0;
}
.container-modal .content-modal .btn-modal:hover {
  box-shadow: 4px 5px 20px 1px rgba(255, 62, 131, 0.831372549);
}
.container-modal .modal-img {
  width: 6rem;
}
.container-modal .mensaje {
  line-height: 1.5;
}

.btn {
  width: 100%;
  padding: 1.5rem;
  border: none;
  border-radius: 1rem;
  background: hsl(234, 29%, 20%);
  margin: 2rem 0;
  color: hsl(0, 0%, 100%);
  font-weight: 700;
}
@media (min-width: 480px) {
  .btn {
    margin: 2rem 0;
  }
}
@media (min-width: 768px) {
  .btn {
    padding: 2rem;
  }
}
.btn:hover {
  background: linear-gradient(90deg, #FF3E83, #F49044);
}